test('Does home screen work', async () => {
  fetch.mockResponseOnce(JSON.stringify(ditto));
  jest.useFakeTimers();
  const handlePress = jest.fn();
  await waitFor(() => {
    render(
      <Pokemon
        details={{
          name: 'ditto',
          url: 'https://pokeapi.co/api/v2/pokemon/1/',
        }}
        press={handlePress}
      />,
    );
  });
  await act(async () => {
    jest.runAllTimers();
  });
  expect(screen.getByText('ditto')).toBeTruthy();
  console.log(screen.getByTestId('sprite').props);
  //console.log(await screen.findByTestId('pressable'));
  fireEvent.press(await screen.findByTestId('pressable'));
  expect(handlePress).toBeCalledWith(ditto);
  expect(screen.getByTestId('sprite').props.source.uri).toEqual(
    'https://raw.githubusercontent.com/PokeAPI/sprites/master/sprites/pokemon/132.png',
  );

  //const pokes = await screen.getAllByTestId('bob');
  //expect(pokes).toHaveLength(3);
});
